.modal {
  display: grid;
  grid-template-columns: 1fr;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  z-index: 100;
  position: fixed;
  top: 22vh;
  left: 25%;
  width: 50%;
  background: white;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 8px;
}

.modal__header {
  width: 95%vw;
  padding: 1rem 0.5rem;
  background: #1c9aae;
  color: white;
}

.modal__header h2 {
  margin: 0.5rem;
}

.modal__content {
  padding: 1rem 0.5rem;
  text-align: center;
  margin-top: 50px;
}

.modal__footer {
  padding: 1rem 0.5rem;
}

@media (max-width: 900px) {
  .modal {
    //left: calc(50% - 20%);
    width: 60vw;
    left: 20%;
  }
}

.modal-enter {
  transform: translateY(-10rem);
  opacity: 0;
}

.modal-enter-active {
  transform: translateY(0);
  opacity: 1;
  transition: all 200ms;
}

.modal-exit {
  transform: translateY(0);
  opacity: 1;
}

.modal-exit-active {
  transform: translateY(-10rem);
  opacity: 0;
  transition: all 200ms;
}

.modal-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
